import styled from 'styled-components';

interface ButtonProps {
  color?: String;
  size?: string;
  disabled?: any;
  active?: boolean;
  label?: String;
  href?: string;
}
const getSize = (size: ButtonProps["size"]) => {
  switch (size) {
    case "1":
      return "auto";
    case "2":
      return "100px";
    case "3":
      return "150px";
    case "4":
      return "250px";
    default: return "auto";
  }
}

const getFontSize = (size: any): string => {
  switch (size) {
    case "1":
      return ".55em";
    case "2":
      return ".7em";
    case "3":
      return "1em";
    case "4":
      return "1.5em";
    case "5":
      return "2em";
    default:
      return ".7em";
  }
}
const Button = styled.button<ButtonProps>`
background-color:${({ theme }) => theme.colors.primary};
position:relative;
color:${({ color }) => color};
padding:5px 10px;
border:none;
font-size:${(props) => getFontSize(props.size)};
outline:none;
box-shadow:0 1px 2px rgba(0,0,0,.2);
text-decoration:none;
transition:.05s;
overflow:hidden;
width:${props => getSize(props.size)};
&:hover {
  background:#8AC23D;
  color:#ffffff;
  box-shadow:0 1px 3px rgba(0,0,0,.5);
  cursor:pointer;
}
& > * {fill: ${({ theme }) => theme.colors.white};}
&:hover > * {fill:${({ theme }) => theme.colors.primary};}
&:active {box-shadow:none; opacity:.8; text-decoration:none; transform:scale(.9);} 

  ${props => props.active && `
    background:#8AC23D;
    color:#ffffff;
    &:hover {
      background:#ffffff;
      color:#8AC23D;
      }
    &:hover > * {fill:#8AC23D;}
  `}
`

export const A = Button.withComponent("a");
export default Button;